// 自定义变量
//颜色
$body-bg:#f4f5f5;
$white: #fff;
$primary:#1e80ff;
$success: #6cbd45;


$text-color:#909090;
$text-search:#666;
$icon-color:#b2bac2;
$border-color:#f1f1f1;
$box-shadow: rgb(0 0 0 / 5%);
$typehead-color:#869aab; //搜索记录
$typehead-list-color:#5e6369; //搜索记录
$title:#2e3135;



// 大小
$header-h: 61px;
$header-btn-h:32px;
$nav-h:45px;
$main-top:15px;
// 全局混入

//单行文本溢出
@mixin nowrap {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

//指定行数溢出
@mixin clamp($num) {
  display: -webkit-box;
  -webkit-line-clamp: $num;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

//禁止选中
@mixin no-select {
  -moz-user-select: none;
  /*火狐*/
  -webkit-user-select: none;
  /*webkit浏览器*/
  -ms-user-select: none;
  /*IE10*/
  -khtml-user-select: none;
  /*早期浏览器*/
  -o-user-select: none;
  /* Opera*/
  user-select: none;
}


// 图片
@mixin imgFit($fit:cover) {
  width: 100%;
  height: 100%;
  object-fit: $fit;
  vertical-align: top;
}

//去除元素移动端点击的背景色
@mixin no-highlight {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
}
// 全局方法
// 重置样式
:root {
  box-sizing: border-box;
  font-family: system-ui,
    -apple-system,
    BlinkMacSystemFont,
    segoe ui,
    Roboto,
    Helvetica,
    Arial,
    sans-serif;
}

*,
::before,
::after {
  box-sizing: inherit;
  font-family: inherit;
}

html,
body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #333;
  background-color: $body-bg;
}

body {
  overflow-y: scroll;
}

.container {
  max-width: 960px;
  margin: auto;

  @media screen and (max-width:980px) {
    width: 96%;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}


// 去除谷歌自动填充input样式-颜色覆盖
input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset;
}

//通知自定义字符图标
.notyf-icon {
  font-size: 21px;
}


//折叠动画
.mu-category-collapse {
  transition: height .25s;
}
//header
@import "@/assets/scss/default/header.scss";
@import "@/assets/scss/default/nav.scss";
@import "@/assets/scss/default/login-dialog.scss";
@import "@/assets/scss/default/fixed-tool.scss";
@import "@/assets/scss/default/drawer-search.scss";

#main {
  padding-top: calc(#{$header-h} + #{$main-top});

  .content {
    position: relative;

    .row {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -10px;


      .col {
        width: 50%;
        padding: 0 10px;
        margin-bottom: 15px;

        @media screen and (max-width:760px) {
          width: 100%;
        }
      }
    }

    .card {
      height: 100%;
      background-color: #fff;
      border-radius: 3px;
      padding: 10px 15px;
      padding-bottom: 0;

      .card-head {
        display: flex;
        align-items: center;

        .left {
          min-width: 0;
          flex-grow: 1;
          display: flex;
          align-items: center;

          .card-img {
            flex-shrink: 0;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            overflow: hidden;
            margin-right: 12px;

            img {
              @include imgFit();
            }
          }

          .card-title {
            .title {
              font-size: 16px;
              line-height: 1;
              font-weight: bold;
              color: #333;

              a {
                color: inherit;
                text-decoration: none;
              }
            }

            .subtitle {
              margin-top: 5px;
              font-size: 13px;
              line-height: 1;
              color: #8a9aa9;
            }
          }
        }

        .right {
          flex-shrink: 0;

          .btn {
            display: inline-block;
            vertical-align: top;
            width: 55px;
            height: 26px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 13px;
            text-decoration: none;
            border: 1px solid #6cbd45;
            color: #6cbd45;

            &:hover {
              opacity: .7;
            }

            &:active {
              opacity: .9;
            }
          }
        }
      }

      .card-body {
        padding: 10px 15px;
        padding-left: 57px;
        font-size: 15px;
        color: #17181a;
        line-height: 1.6;
        word-wrap: break-word;
        height: 92px;

        .card-text {
          @include clamp(3);

          img {
            width: 25px;
            height: 25px;
            vertical-align: sub;
            margin-left: 4px;

            &+img {
              margin-right: 2px;
            }
          }
        }
      }

      .card-footer {
        display: flex;
        align-items: center;
        border-top: 1px solid #ebebeb;
        margin: 0 -15px;
        padding: 5px 0;

        a {
          min-width: 0;
          flex-grow: 1;
          color: #919AA6;
          height: 24px;
          font-size: 12px;
          text-decoration: none;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;


          &:first-child {
            border-right: 1px solid #ebebeb;
          }

          &:last-child {
            border-left: 1px solid #ebebeb;
          }

          &:hover {
            opacity: .7;
          }

          .icon {
            margin-right: 5px;
          }
        }
      }
    }


    //占位
    .placeholder {
      height: 600px;
      background-color: #fff;
      display: flex;

      .placeholder-content {
        margin: auto;

        .img-warp {
          width: 240px;
          height: 240px;
          margin: 0 auto;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            vertical-align: top;
          }
        }

        .text {
          text-align: center;
          color: $text-color;
          font-size: 16px;
        }
      }


    }
  }

}